.mx-tooltip:before {
  content: attr(data-tooltip);
  background-color: #333;
  color: #fff;
  font-size: 0.75rem;
  text-align: center;
  padding: 0.25rem 0.75rem;
  top: 100%;
  left: 50%;
  width: 7rem;
  margin: 0.5rem 0 0 -3.5rem;
  border-radius: 2px;
}
.mx-tooltip:after {
  content: "";
  border: 0.375rem solid transparent;
  width: 0;
  height: 0;
  border-bottom-color: #333;
  margin-left: -0.375rem;
  top: 100%;
  left: 50%;
  margin-top: -0.25rem;
}
.mx-tooltip-align-left:before {
  left: 0;
  margin-left: 0;
}
.mx-tooltip-align-left:after {
  left: 20%;
}
.mx-tooltip-align-right:before {
  left: auto;
  right: 0;
}
.mx-tooltip-align-right:after {
  left: 80%;
}
.mx-tooltip-align-top:before {
  top: auto;
  bottom: 100%;
  margin: 0 0 0.5rem -3.5rem;
}
.mx-tooltip-align-top:after {
  top: auto;
  bottom: 100%;
  margin-bottom: -0.25rem;
  border-color: #333 transparent transparent transparent;
}
.mx-tooltip:hover:before,
.mx-tooltip:hover:after {
  opacity: 1;
  visibility: visible;
}
span.tippy-trigger {
  color: #0076d6;
  cursor: pointer;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s;
}
span.tippy-trigger:hover,
span.tippy-trigger:active,
span.tippy-trigger:focus {
  color: #53a318;
  text-decoration: underline;
}
.tippy-tooltip.mixer-theme {
  background-color: #fff;
  border: 2px solid #333;
  color: #333;
  padding: 1rem;
  text-align: left;
  border-radius: 2px;
}
.tippy-tooltip.mixer-theme .tippy-backdrop {
  background-color: #fff;
}
.mx-tooltip-no-caret:after {
  display: none;
}
.background-gray-600,
.background-groupon-gray {
  background-color: #707174;
}
.background-black,
.background-groupon-black {
  background-color: #333;
}
.background-green,
.background-groupon-green {
  background-color: #53a318;
}
.background-green-050 {
  background-color: #eafcde;
}
.background-gray {
  background-color: #f6f7f8;
}
.text-center {
  text-align: center;
}
.space-reset {
  margin: 0;
  padding: 0;
}
.mx-tile {
  background-color: #fff;
  border: 1px solid #d5d8db;
  -webkit-box-shadow: 2px 2px 0 0 #d5d8db;
          box-shadow: 2px 2px 0 0 #d5d8db;
  padding: 1rem;
  margin-bottom: 1.5rem;
}
.mx-tile-header {
  font-size: 1.125rem;
  line-height: 1.5rem;
  font-weight: 600;
  margin: 0 0 1.5rem 0;
}



